import { Component, ViewEncapsulation, OnInit } from '@angular/core'
import { fuseAnimations } from '@fuse/animations'
import { Router } from '@angular/router'
import { SubscriptionLike } from 'rxjs'

import { MatDialog } from '@angular/material'

import { UtilityService } from 'app/services/utility.service'

import { DialogAddTruck } from './dialog/dialog-add-truck/dialog-add-truck'

import { DialogEditTruck } from './dialog/dialog-edit-truck/dialog-edit-truck'

@Component({
    selector: 'list-truck',
    templateUrl: './list-truck.component.html',
    styleUrls: ['./list-truck.component.scss'],
    providers: [
        UtilityService
    ],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class ListTruckComponent implements OnInit {
    isLoading: boolean
    isSuccessFetch: boolean
    reverseListTruck: boolean

    orderListTruck: string
    itemListTruck: string
    failMessage: string

    pageListTruck: number

    filteredListTruck = []
    arrListTruck = []

    timeout: NodeJS.Timer
    subLoadListTruck: SubscriptionLike

    constructor(
        private router: Router,
        private dialog: MatDialog,

        private utilityService: UtilityService
    ) {
        this.orderListTruck = "id"
        this.itemListTruck = "10"
        this.pageListTruck = 1
    }

    ngOnInit(): void {
        this.isLoading = false
        this.isSuccessFetch = true
        this.filteredListTruck = listTruck
        this.arrListTruck = listTruck
    }

    ngOnDestroy(): void {
        clearTimeout(this.timeout)

        if (this.subLoadListTruck) {
            this.subLoadListTruck.unsubscribe()
        }
    }

    timeoutCondition(hitApi: any) {
        hitApi.unsubscribe()
        clearTimeout(this.timeout)

        this.isLoading = false
        this.isSuccessFetch = false

        this.failMessage = "Timeout, Mohon periksa koneksi internet anda"
    }

    filterListTruck(filterValue: string) {
        filterValue = filterValue.trim()
        filterValue = filterValue.toLowerCase()

        this.arrListTruck = this.filteredListTruck.filter(item => item.merk.includes(filterValue))
    }

    setOrderListTruck(value: string) {
        if (this.orderListTruck === value) {
            this.reverseListTruck = !this.reverseListTruck;
        }

        this.orderListTruck = value
    }

    addTruckDialog() {
        this.dialog.open(DialogAddTruck, {
            width: "500px",
            disableClose: true
        })
    }

    editTruckDialog(description: string, credit: number) {
        this.dialog.open(DialogEditTruck, {
            width: "500px",
            disableClose: true,
            data: {
                description: description,
                credit: credit
            }
        })
    }
}

const listTruck = [
    {
        id: 1,
        nopol: "B 1232 ABC",
        merk: "hino",
        model: "TR-132 TRD",
        vendor_name: "PT. ABCDERF",
        status: "Aktif",
        kelengkapan: "STNK, INTERIOR, DEPAN, SAMPING"
    }
]